import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { makeStyles } from '@material-ui/styles';
import moment from 'moment';
import {
	Card,
	CardActions,
	CardContent,
	CardHeader,
	colors,
	Divider,
	IconButton,
	Link,
	Table,
	TableBody,
	TableCell,
	TableHead,
	TableSortLabel,
	TablePagination,
	TableRow,
	Typography
} from '@material-ui/core';

import ArrowForwardRoundedIcon from '@material-ui/icons/ArrowForwardRounded';

import NumberFormat from 'react-number-format';

const locale = require('react-redux-i18n').I18n;

const useStyles = makeStyles((theme) => ({
	root: {},
	content: {
		padding: 0
	},
	inner: {
		minWidth: 700
	},
	nameCell: {
		display: 'flex',
		alignItems: 'center'
	},
	avatar: {
		height: 42,
		width: 42,
		marginRight: theme.spacing(1)
	},
	label: {
		color: colors.grey[900],
		padding: theme.spacing(0, 1.5),
		fontWeight: 'bold'
	},
	actions: {
		padding: theme.spacing(1),
		justifyContent: 'flex-end'
	}
}));

const Results = (props) => {
	const headerCells = [
		{
			id: 'no', label: locale.t('shopping.customer_orders.item_col'), align: 'left', sorting: true
		},
		{
			id: 'total_price', label: locale.t('shopping.customer_orders.item_col2'), align: 'right', sorting: true
		},
		{
			id: 'created_date', label: locale.t('shopping.customer_orders.item_col4'), align: 'center', sorting: true
		},
		{
			id: 'action', label: locale.t('shopping.customer_orders.item_col5'), align: 'left', sorting: false
		},
	];
	const [order, setOrder] = useState('asc');
	const [orderBy, setOrderBy] = useState(headerCells[0].id);

	//	Constants
	const {
		className,
		objects,
		total,
		onChangePage,
		onChangeRowsPerPage,
		onSorting,
		page,
		rows,
		...rest
	} = props;

	const classes = useStyles();

	const [rowsPerPage, setRowsPerPage] = useState(rows);

	const handleChangePage = (event, nextPage) => {
		onChangePage(nextPage);
	};

	const handleChangeRowsPerPage = (event) => {
		setRowsPerPage(event.target.value);
		onChangeRowsPerPage(event.target.value);
	};

	const handleRequestSort = (event, property) => {
		const isAsc = orderBy === property && order === 'asc';
		setOrder(isAsc ? 'desc' : 'asc');
		setOrderBy(property);
		onSorting(order, property);
	};

	const createSortHandler = (property) => (event) => {
		handleRequestSort(event, property);
	};

	return (
		<div
			{...rest}
			className={clsx(classes.root, className)}
		>
			<Typography
				color="textSecondary"
				gutterBottom
				variant="body2"
			>
				{total}
				{locale.t('result_table.hint')}
				{page + 1}
				{locale.t('result_table.hint2')}
				{Math.ceil(total / rowsPerPage)}
			</Typography>
			<Card>
				<CardHeader
					title={locale.t('shopping.customer_orders.result_title')}
				/>
				<Divider />
				<CardContent className={classes.content}>
					<PerfectScrollbar>
						<div className={classes.inner}>
							<Table>
								<TableHead>
									<TableRow>
										{headerCells.map((headerCell) => (
											<TableCell
												key={headerCell.id}
												align={headerCell.align ? headerCell.align : 'center'}
												sortDirection={orderBy === headerCell.id ? order : false}
											>
												{ headerCell.sorting && (
													<TableSortLabel
														active={orderBy === headerCell.id}
														direction={orderBy === headerCell.id ? order : 'asc'}
														onClick={createSortHandler(headerCell.id)}
													>
														{headerCell.label}
													</TableSortLabel>
												)}
												{ !headerCell.sorting && headerCell.label}
											</TableCell>
										))}
									</TableRow>
								</TableHead>
								<TableBody>
									{objects.slice(0, rowsPerPage).map((object) => (
										<TableRow
											hover
											key={object.id}
										>
											<TableCell>
												<div className={classes.nameCell}>
													<div>
														<Link
															color="inherit"
															component={RouterLink}
															to={`/member/orders/${object.id}`}
															variant="h6"
														>
															<strong>{object.no}</strong>
														</Link>
													</div>
												</div>
											</TableCell>
											<TableCell align="right">
												<NumberFormat value={object.total.toNumber()} displayType="text" prefix="à¸¿" thousandSeparator />
											</TableCell>
											<TableCell align="center">
												{moment.unix(object.dateTime.toNumber()).format('DD MMMM yyyy HH:mm')}
											</TableCell>
											<TableCell align="left">
												<IconButton
													aria-label="item-view"
													component={RouterLink}
													size="small"
													to={`/member/orders/${object.id}`}
													variant="outlined"
												>
													<ArrowForwardRoundedIcon />
												</IconButton>
											</TableCell>
										</TableRow>
									))}
								</TableBody>
							</Table>
						</div>
					</PerfectScrollbar>
				</CardContent>
				<CardActions className={classes.actions}>
					<TablePagination
						component="div"
						count={total}
						labelDisplayedRows={({ from, to, count }) => `${from}-${to === -1 ? count : to} ${locale.t('result_table.page_hint')} ${count !== -1 ? count : `${locale.t('result_table.page_hint2')} ${to}`}`}
						labelRowsPerPage={locale.t('result_table.rows_per_page')}
						onChangePage={handleChangePage}
						onChangeRowsPerPage={handleChangeRowsPerPage}
						page={page}
						rowsPerPage={rowsPerPage}
						rowsPerPageOptions={[5, 10, 25]}
					/>
				</CardActions>
			</Card>
		</div>
	);
};

Results.propTypes = {
	className: PropTypes.string,
	objects: PropTypes.array,
	onChangePage: PropTypes.func,
	onChangeRowsPerPage: PropTypes.func,
	onSorting: PropTypes.func,
	page: PropTypes.number,
	rows: PropTypes.number,
	total: PropTypes.number
};

Results.defaultProps = {
	objects: [],
	total: 0
};

export default Results;
