import React, { useState, useEffect } from 'react';
import moment from 'moment';
import { makeStyles } from '@material-ui/styles';
import {
	Container
} from '@material-ui/core';

import Page from 'src/components/Page';
import SearchBar from './SearchBar';
import Header from './Header';
import Results from './Results';
import Placeholder from './Placeholder';
import Loading from './Loading';

import { useWeb3React } from '@web3-react/core';
import { Contract } from '@ethersproject/contracts';
import { InjectedConnector } from '@web3-react/injected-connector';

import ERC20ABI from 'src/configs/abi.json';

import * as Constant from 'src/configs/Constant';

const locale = require('react-redux-i18n').I18n;

const useStyles = makeStyles((theme) => ({
	root: {
		padding: theme.spacing(3, 0)
	},
	results: {
		marginTop: theme.spacing(3)
	}
}));

const OrderList = () => {
	const classes = useStyles();
	const [objects, setObjects] = useState(null);
	const [total, setTotal] = useState(0);
	const [values, setValues] = useState({
		search: '',
		type: 'member',
		page: 0,
		sort_by: 'created_date',
		sort_order: 'desc',
		rows_per_page: 10
	});

	const {
		chainId, account, activate, active, library
	} = useWeb3React();
	const injectedConnector = new InjectedConnector({
		supportedChainIds: Constant.SUPPORTED_CHAIN_IDS,
	});

	//	Functions

	useEffect(() => {
		//	Web3 activate connector
		activate(injectedConnector);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	useEffect(() => {
		if (chainId && active) {
			const fetchObjects = async () => {
				const address = Constant.SMART_CONTRACT_ADDR;
				const contract = new Contract(address, ERC20ABI, library.getSigner());
				const orders = await contract.getAllOrder(account);
				setObjects(orders);
				console.log('objects', orders);
				setTotal(orders.length);
			};
			fetchObjects();
		}
	// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [chainId]);

	const handleSearch = (search) => {
		const newValues = {
			...values,
			search: search.query,
			status: search.status,
			start_date: moment(search.startDate).format('yyyy-MM-DD'),
			end_date: moment(search.endDate).format('yyyy-MM-DD'),
			page: 0
		};
		if (newValues.status === -1) {
			delete newValues.status;
		}
		setValues(newValues);
	};
	const handleSorting = (sortOrder, sortBy) => {
		setValues({
			...values,
			sort_by: sortBy,
			sort_order: sortOrder
		});
	};
	const handleChangePage = (page) => {
		setValues({
			...values,
			page
		});
	};
	const handleChangeRowsPerPage = (rows) => {
		setValues({
			...values,
			rows_per_page: rows
		});
	};

	return (
		<Page
			className={classes.root}
			title={locale.t('shopping.customer_orders.title')}
		>
			<Container maxWidth="lg">
				<Header />
				<SearchBar
					onSearch={handleSearch}
				/>
				{!objects && (
					<Loading
						className={classes.results}
					/>
				)}
				{objects && objects.length === 0 && (
					<Placeholder />
				)}
				{objects && objects.length > 0 && (
					<Results
						className={classes.results}
						objects={objects}
						onChangePage={handleChangePage}
						onChangeRowsPerPage={handleChangeRowsPerPage}
						onSorting={handleSorting}
						page={values.page}
						rows={values.rows_per_page}
						total={total}
					/>
				)}
			</Container>
		</Page>
	);
};

export default OrderList;
