import React from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { makeStyles } from '@material-ui/styles';
import {
	Card,
	CardActions,
	CardContent,
	CardHeader,
	Divider,
	Table,
	TableBody,
	TableCell,
	TableHead,
	TableSortLabel,
	TableRow,
	Typography
} from '@material-ui/core';
import {
	Skeleton
} from '@material-ui/lab';

import _ from 'lodash';

const locale = require('react-redux-i18n').I18n;

const useStyles = makeStyles((theme) => ({
	root: {},
	content: {
		padding: 0
	},
	inner: {
		minWidth: 700
	},
	actions: {
		padding: theme.spacing(2),
		justifyContent: 'flex-end'
	}
}));

const Loading = (props) => {
	const headerCells = [
		{
			id: 'no', label: locale.t('shopping.customer_orders.item_col'), align: 'left', sorting: true
		},
		{
			id: 'total_price', label: locale.t('shopping.customer_orders.item_col2'), align: 'left', sorting: true
		},
		{
			id: 'status', label: locale.t('shopping.customer_orders.item_col3'), align: 'center', sorting: true
		},
		{
			id: 'created_date', label: locale.t('shopping.customer_orders.item_col4'), align: 'left', sorting: true
		},
		{
			id: 'action', label: locale.t('shopping.customer_orders.item_col5'), align: 'left', sorting: false
		},
	];

	//	Constants
	const {
		className,
		...rest
	} = props;

	const classes = useStyles();

	return (
		<div
			{...rest}
			className={clsx(classes.root, className)}
		>
			<Typography
				color="textSecondary"
				gutterBottom
				variant="body2"
			>
				{locale.t('result_table.loading')}
			</Typography>
			<Card>
				<CardHeader
					title={locale.t('shopping.customer_orders.result_title')}
				/>
				<Divider />
				<CardContent className={classes.content}>
					<PerfectScrollbar>
						<div className={classes.inner}>
							<Table>
								<TableHead>
									<TableRow>
										{headerCells.map((headerCell) => (
											<TableCell
												key={headerCell.id}
												align={headerCell.align ? headerCell.align : 'center'}
											>
												{ headerCell.sorting && (
													<TableSortLabel>
														{headerCell.label}
													</TableSortLabel>
												)}
												{ !headerCell.sorting && headerCell.label}
											</TableCell>
										))}
									</TableRow>
								</TableHead>
								<TableBody>
									{_.times(10, (i) => (
										<TableRow
											hover
											key={i}
										>
											<TableCell>
												<Skeleton animation="wave" width={220} height={24} />
											</TableCell>
											<TableCell align="left">
												<Skeleton animation="wave" width={100} height={24} />
											</TableCell>
											<TableCell align="left">
												<Skeleton animation="wave" width={70} height={24} />
											</TableCell>
											<TableCell align="left">
												<Skeleton animation="wave" width={150} height={24} />
											</TableCell>
											<TableCell align="left">
												<Skeleton animation="wave" variant="circle" width={32} height={32} />
											</TableCell>
										</TableRow>
									))}
								</TableBody>
							</Table>
						</div>
					</PerfectScrollbar>
				</CardContent>
				<CardActions className={classes.actions}>
					<Skeleton animation="wave" height={24} width={200} style={{ marginRight: 16 }} />
					<Skeleton animation="wave" variant="circle" width={32} height={32} />
					<Skeleton animation="wave" variant="circle" width={32} height={32} />
				</CardActions>
			</Card>
		</div>
	);
};

Loading.propTypes = {
	className: PropTypes.string,
};

export default Loading;
