import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import moment from 'moment';
import { makeStyles } from '@material-ui/styles';
import {
	Divider,
	Grid,
	Input,
	Card,
	MenuItem,
	TextField
} from '@material-ui/core';
import {
	KeyboardDatePicker,
} from '@material-ui/pickers';

import SearchIcon from '@material-ui/icons/Search';

const locale = require('react-redux-i18n').I18n;

const useStyles = makeStyles((theme) => ({
	root: {},
	keywords: {
		padding: theme.spacing(2),
		display: 'flex',
		alignItems: 'center'
	},
	searchIcon: {
		color: theme.palette.icon,
		marginRight: theme.spacing(2)
	},
	input: {
		width: '100%'
	},
	selects: {
		padding: theme.spacing(2),
		'& > * + *': {
			marginLeft: theme.spacing(2)
		}
	}
}));

function Search({ className, onSearch, ...rest }) {
	const classes = useStyles();
	const [formState, setFormState] = useState({
		values: {
			query: '',
			status: -1,
		}
	});
	const [selectedDate, setSelectedDate] = useState({
		startDate: moment(),
		endDate: moment()
	});
	const options = [
	];

	//	Functions

	const handleChange = (event) => {
		event.persist();

		const newFormState = {
			...formState,
			values: {
				...formState.values,
				[event.target.name]:
					event.target.type === 'checkbox'
						? event.target.checked
						: event.target.value
			},
			touched: {
				...formState.touched,
				[event.target.name]: true
			}
		};
		setFormState(newFormState);

		//	Callback
		if (event.target.name === 'status') {
			onSearch({ ...selectedDate, ...newFormState.values });
		}
	};

	const handleDateChange = (date, code) => {
		const newDate = { ...selectedDate };
		if (code === 'startDate') {
			if (date > newDate.endDate.valueOf()) {
				newDate.endDate = date;
			}
		}
		if (code === 'endDate') {
			if (date < newDate.startDate.valueOf()) {
				newDate.startDate = date;
			}
		}
		const newSelectedDate = {
			...newDate,
			[code]: date
		};
		setSelectedDate(newSelectedDate);

		//	Callback
		onSearch({ ...newSelectedDate, ...formState.values });
	};

	const handleInputKeyup = (event) => {
		event.persist();

		if (event.keyCode === 13) {
			//	Callback
			onSearch({ ...selectedDate, ...formState.values });
		}
	};

	return (
		<Card
			{...rest}
			className={clsx(classes.root, className)}
		>
			<div className={classes.keywords}>
				<SearchIcon className={classes.searchIcon} />
				<Input
					className={classes.input}
					disableUnderline
					name="query"
					onChange={handleChange}
					onKeyUp={handleInputKeyup}
					placeholder={locale.t('shopping.customer_orders.search.search_placeholder')}
					value={formState.values.query || ''}
				/>
			</div>
			<Divider />
			<div className={classes.selects}>
				<Grid
					container
					spacing={3}
				>
					<Grid
						item
						md={3}
						xs={12}
					>
						<TextField
							fullWidth
							label={locale.t('shopping.customer_orders.search.status')}
							name="status"
							onChange={handleChange}
							select
							value={formState.values.status || ''}
							variant="outlined"
						>
							<MenuItem
								key={-1}
								value={-1}
							>
								{locale.t('shopping.customer_orders.search.all_status')}
							</MenuItem>
							{options.map((option) => (
								<MenuItem
									key={option.id}
									value={option.id}
								>
									{option.name}
								</MenuItem>
							))}
						</TextField>
					</Grid>
					<Grid
						item
						md={3}
						xs={6}
					>
						<KeyboardDatePicker
							disableToolbar
							inputVariant="outlined"
							variant="inline"
							format="DD/MM/yyyy"
							label={locale.t('shopping.customer_orders.search.start_date')}
							value={selectedDate.startDate}
							onChange={(date) => handleDateChange(date, 'startDate')}
							KeyboardButtonProps={{
								'aria-label': 'change date',
							}}
						/>
					</Grid>
					<Grid
						item
						md={3}
						xs={6}
					>
						<KeyboardDatePicker
							disableToolbar
							inputVariant="outlined"
							variant="inline"
							format="DD/MM/yyyy"
							label={locale.t('shopping.customer_orders.search.end_date')}
							value={selectedDate.endDate}
							onChange={(date) => handleDateChange(date, 'endDate')}
							KeyboardButtonProps={{
								'aria-label': 'change date',
							}}
						/>
					</Grid>
				</Grid>
			</div>
		</Card>
	);
}

Search.propTypes = {
	className: PropTypes.string,
	onSearch: PropTypes.func
};

export default Search;
